	module.exports = {"data":{"markdownRemark":{"html":"<p>Moved to the first oversea server, <a href=\"http://www.virtualave.net/\">VirtualAvenue</a> , renewed both together. (I used HOOPS as a mirror site → I was also using service termination) I am tired of studying for an exam ( <a href=\"/2957\">So I slipped w</a> ) I will start to get into graphics <a href=\"/2957\">again</a> . Until now I used \" <a href=\"http://www10.plala.or.jp/kiriman/\">HyperPaint</a> \" for full color graphics creation, but from around <a href=\"http://www.pixia.jp/\">here</a> I start using \" <a href=\"http://www.pixia.jp/\">PIXIA</a> \" from about 2005. <img src=\"https://danmaq.com/wp-content/uploads/1999/09/yasim3.jpg\"><img src=\"https://danmaq.com/wp-content/uploads/1999/09/yasim4.jpg\" alt=\"yasim 4\"> Background color red tea (Refreshing the logo in the latter term and blue green base). At that time, Hiroshi Watanabe familiar with beatmania was adored to the design of the site by a well-done person. Especially, he liked the one whose link was a pop-up list box which used scripts together. Up to this point, I used Dreamwaver of Macromedia (now Adobe) for HTML construction. With around the year 2000, I will write HTML only with a text editor all the time.</p>","frontmatter":{"cover":null,"date":"1999-09-24T00:00:00+09:00","strDate":"1999/9/23","title":"YASiM-CITY"}}},"pathContext":{"path":"/en/blog/1999/09/24-yasim-city/","langKey":"en"}}

